import { React, Fragment, useState, useLayoutEffect } from 'react';
import { Redirect } from 'react-router-dom';
import NotFound from '../../Auth/NotFound/NotFound';
import MaterialTable from 'material-table';
import RadioGroup from '../../../components/Controls/RadioGroup';
import Spinner from '../../../components/UI/Spinner/Spinner';
import axios from 'axios';

const attendanceStatusItems = [
  { id: 'P', title: 'P' },
  { id: 'A', title: 'A' },
];

const styles = {
  maxWidth: '90%',
  margin: '1em auto',
};

const AttendanceInstructor = (props) => {
  const [auxRegister, setAuxRegister] = useState(false);
  const [loading, setLoading] = useState(false);
  const [data, setData] = useState([]);
  const [notFound, setNotFound] = useState(false);

  useLayoutEffect(() => {
    request();
    // eslint-disable-next-line
  }, [auxRegister]);

  const request = () => {
    setLoading((loading) => !loading);
    axios
      .get(
        '/attendance-instructor/' +
          props.match.params.group +
          '/' +
          props.match.params.date
      )
      .then((response) => {
        console.log(response.data);
        setData(response);
        setLoading((loading) => !loading);
      })
      .catch((error) => {
        setLoading((loading) => !loading);
        if (error.response) {
          //Redirige si el usuario no esta inscrito a cierto grupo o si el grupo no existe
          if (error.response.data.message == 'Pagina no encontrada') {
            setNotFound(true);
          } else {
            //Se realizo la peticion y el servidor respondio con un codigo de status
            window.alert('Error: ' + error.response);
          }
        } else if (error.request) {
          //Se realizo la peticion pero no se recibio una respuesta
          window.alert('Error: ' + error.request);
        } else {
          //Algo ocurrio al preparar la peticion que desencadeno un error
          console.log('Error: ' + error.message);
        }
        console.log(error.config);
      });
  };

  const handleRowUpdate = (newData, status, resolve) => {
    //url = `/auth/attendance-instructor/set-attendance/${props.match.params.group}/${props.match.params.date}/${newData.rfc}`, {status}
    axios
      .put(`/auth/attendance-instructor/set-attendance/${props.match.params.group}/${props.match.params.date}/${newData.rfc}`, {
        status,
      })
      .then((res) => {
        setAuxRegister((auxRegister) => !auxRegister);
        resolve();
      })
      .catch((error) => {
        window.alert('Error: ' + error.response.data.message);
      });
  };

  let courseEnrollmentTable = (
    <Fragment>
      <MaterialTable
        title={'Asistencia del dia: ' + props.match.params.date}
        columns={[
          { title: 'Nombre', field: 'name', editable: 'never' },
          {
            title: 'Estado',
            field: 'status',
            editComponent: (props) => (
              <RadioGroup
                name='attendanceStatus'
                value={props.value}
                onChange={(e) => {
                  props.onChange(e.target.value);
                }}
                items={attendanceStatusItems}
              />
            ),
          },

          { title: 'Active', field: 'active', hidden: true },
          { title: 'RFC', field: 'rfc', hidden: true },
        ]}
        data={data.data}
        editable={{
          onRowUpdate: (newData) =>
            new Promise((resolve) => {
              handleRowUpdate(newData, newData.status, resolve);
            }),
        }}
        options={{
          actionsColumnIndex: -1,
          pageSize: 30,
          pageSizeOptions: [30, 50],
          headerStyle: {
            backgroundColor: '#7395ae',
            color: '#FFF',
            fontSize: '13px',
            fontWeight: 'bold',
          },
          filterCellStyle: {
            backgroundColor: '#01579b',
            color: '#FFF',
          },
          searchFieldStyle: {
            backgroundColor: '#E8E8E8',
            color: 'black',
          },
        }}
        localization={{
          header: {
            actions: 'TOMAR ASISTENCIA',
          },
          toolbar: {
            searchTooltip: 'Buscar',
            searchPlaceholder: 'Buscar',
          },
          pagination: {
            labelDisplayedRows: '{from}-{to} de {count}',
            labelRowsSelect: 'Filas',
            firstAriaLabel: 'Primera pagina',
            firstTooltip: 'Primera pagina',
            previousAriaLabel: 'Pagina anterior',
            previousTooltip: 'Pagina anterior',
            nextAriaLabel: 'Siguiente pagina',
            nextTooltip: 'Siguiente pagina',
            lastAriaLabel: 'Ultima pagina',
            lastTooltip: 'Ultima pagina',
          },
          body: {
            emptyDataSourceMessage: 'No cuenta con cursos asignados',
          },
          actions: {
            tooltip: 'Tomar asistencia',
          },
        }}
        // cellEditable={{
        //   onCellEditApproved: (newData, oldData, rowData, columnDef) => {
        //     return new Promise((resolve) => {
        //       handleRowUpdate(newData, resolve);
        //     });
        //   },
        // }}
      />
    </Fragment>
  );

  if (loading) {
    courseEnrollmentTable = <Spinner />;
  }

  if (notFound) {
    courseEnrollmentTable = <Redirect to='/notFound' component={NotFound} />;
  }

  return <div style={styles}>{courseEnrollmentTable}</div>;
};

export default AttendanceInstructor;
